import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import CssBaseline from "@material-ui/core/CssBaseline";
import Home from "./pages/home";
import Mint from "./pages/mint";
import Nav from "./pages/navbar";
import Solana from "./pages/solana/App";

function App() {
  return (
    <Router>
      <CssBaseline>
        <Nav />
      </CssBaseline>

      <Switch>
        <Route exact path="/" render={(props) => <Home {...props} />} />
      </Switch>
      <Switch>
        <Route path="/mint" render={(props) => <Mint {...props} />} />
      </Switch>
      <Switch>
        <Route path="/solana" render={(props) => <Solana {...props} />} />
      </Switch>
    </Router>
  );
}

export default App;
