import React, { useState, useEffect } from "react";
import Moralis from "../moralis";
import { useSnackbar } from "notistack";
import { useSelector } from "react-redux";
import { mintNFT, batchMintNFT } from "../blockchain/functions";
import { Grid, Box, TextField, Button } from "@material-ui/core";

// const ipfsClient = create("https://ipfs.infura.io:5001/api/v0");

function Mint() {
  const [collectionToSearch, setCollectionToSearch] = useState("");
  const [currentCollection, setCurrentCollection] = useState("");
  const [lastestCollections, setLastestCollections] = useState([]);
  const [batchAmount, setBatchAmount] = useState(0);
  const [NFTminted, setNFTminted] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const user = useSelector((state) => state.account.userAddress);
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  useEffect(() => {
    getLastestCollections();
  }, []);

  const getOneCollection = async () => {
    const Collection = Moralis.Object.extend("Collections");
    const query = new Moralis.Query(Collection);

    query.equalTo("name", `${collectionToSearch}`);
    const results = await query.first();

    if (results) {
      const { name, items } = results.attributes;
      console.log(name, items);
      setCurrentCollection({ name, items });
    } else {
      enqueueSnackbar("collection not found", {
        variant: "error",
      });
      console.log("collection not found");
    }
  };

  const getLastestCollections = async () => {
    const Collection = Moralis.Object.extend("Collections");
    const query = new Moralis.Query(Collection);

    const results = await query.find();
    if (results.length > 0) {
      setLastestCollections(results);
      console.log(results);
    } else {
      console.log("lastest collections not found");
    }
  };

  const updateMintedCounter = async (num) => {
    const Collection = Moralis.Object.extend("Collections");
    const query = new Moralis.Query(Collection);

    query.equalTo("name", `${collectionToSearch}`);
    const result = await query.first();

    result.increment("minted", num);
    result.save();
  };

  const getRandomNFT = async () => {
    let randomNum = Math.floor(Math.random() * currentCollection.items);
    console.log(randomNum);
    const Collection = Moralis.Object.extend(`${currentCollection.name}`);
    const query = new Moralis.Query(Collection);

    query.equalTo("tokenId", randomNum);
    const result = await query.first();
    return result;
  };

  const mint = async () => {
    setIsLoading(true);
    try {
      let NFTdetails = [];
      let result = await getRandomNFT();
      console.log(result);
      const { metadata, minted, image } = result.attributes;
      console.log(metadata, minted);
      if (!minted) {
        NFTdetails.push(image);
        let receipt = await mintNFT(metadata);
        if (receipt.events.Transfer) {
          console.log(receipt);
          updateMintedCounter(1);
          setNFTminted(NFTdetails);
        } else {
          console.log("Something went wrong");
        }
      } else {
        console.log("already minted, trying another");
        mint();
      }
    } catch (error) {
      console.log(error);
    }
    setIsLoading(false);
  };

  const batchMint = async () => {
    setIsLoading(true);
    if (batchAmount <= 10 && batchAmount > 0) {
      try {
        let NFTsDetails = [];
        let uris = [];
        for (; uris.length < batchAmount; ) {
          let result = await getRandomNFT();
          console.log(result);
          const { metadata, minted, image } = result.attributes;
          if (!minted) {
            uris.push(metadata);
            NFTsDetails.push(image);
          } else {
            return;
          }
        }
        if (uris.length === Number(batchAmount)) {
          console.log("minting");
          let receipt = await batchMintNFT(uris);
          if (receipt.events.Transfer) {
            console.log(receipt);
            updateMintedCounter(Number(batchAmount));
            setNFTminted(NFTsDetails);
          } else {
            console.log("something went wrong");
          }
        } else {
          console.log("something went wrong");
          //   mint();
        }
      } catch (error) {
        console.log(error);
      }
    } else {
      window.alert(
        "this is a test site, maximum of batch is 10, please contact the site developper for assistance"
      );
    }
    setIsLoading(false);
  };

  return (
    <Grid container spacing={3} className="mint-container">
      <Grid xs={12} md={4} className="mint-left-container">
        <Box className="collection-search-box">
          <h3>Search collection</h3>
          <TextField
            value={collectionToSearch}
            onChange={(e) => setCollectionToSearch(e.target.value)}
          />
          <Button variant="outlined" onClick={() => getOneCollection()}>
            Search
          </Button>
        </Box>

        <Box className="last-collections-box">
          <h2>Last Collections Created</h2>
          {lastestCollections.length > 0 &&
            lastestCollections.map((el, index) => {
              return (
                <>
                  <h3 key={index}>{el.attributes.name}</h3>
                  <h5 key={index + 100}>
                    {" "}
                    {el.attributes.items} items on this collection:{" "}
                    {el.attributes.minted} NFT already Minted{" "}
                  </h5>
                </>
              );
            })}
        </Box>
      </Grid>
      <Grid xs={0} md={8} className="mint-right-container">
        <Grid container direction="column" className="mint-collection">
          <Grid
            container
            direction="column"
            justifyContent="flex-start"
            alignItems="center"
            className="mint-title-box"
          >
            <h2>NFT Minter</h2>

            {currentCollection ? (
              <h3>
                You are minting from the collection: {currentCollection.name}.
                {"     "}There are {currentCollection.items} NFTs in this
                collection.{" "}
              </h3>
            ) : (
              <h3> Please choose a collection to mint from. </h3>
            )}

            {NFTminted.length > 0 ? (
              <h4>
                {" "}
                Succesfully Minted {NFTminted.length} NFTs, you can see them on
                OpenSea.{" "}
              </h4>
            ) : user ? (
              <h4>Wallet Connected {user}</h4>
            ) : (
              <h4>Connect Your Wallet to Mint</h4>
            )}
          </Grid>

          <Grid
            container
            direction="row"
            justifyContent="space-around"
            alignItems="center"
            className="mint-images-box"
          >
            {NFTminted.length > 0 &&
              NFTminted.map((el, index) => {
                return (
                  <img
                    className="minted-image"
                    src={el}
                    alt={index}
                    key={index}
                    style={{ maxHeight: "200px", maxWidth: "200px" }}
                  />
                );
              })}
          </Grid>
        </Grid>

        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
          className="mint-form"
        >
          <Grid className="batch-mint">
            <h3> Batch Mint Random NFTs </h3>
            <TextField
              label="Amount"
              type="number"
              variant="outlined"
              value={batchAmount}
              style={{ "max-width": "80px" }}
              onChange={(e) => setBatchAmount(e.target.value)}
            />
            <br />
            <Button
              variant="outlined"
              disabled={!isLoading && currentCollection ? 0 : 1}
              onClick={() => batchMint()}
            >
              {isLoading ? "Minting..." : "Batch Mint"}
            </Button>
          </Grid>
          <Grid className="single-mint">
            <h3> Mint 1 Random NFT</h3>
            <Button
              variant="outlined"
              disabled={!isLoading && currentCollection ? 0 : 1}
              style={{ "margin-bottom": "5px" }}
              onClick={() => mint()}
            >
              {isLoading ? "Minting..." : "Mint"}
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Mint;
