import React from "react";
import {
  AppBar,
  Box,
  IconButton,
  Button,
  Typography,
  Grid,
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../state/index";
import Moralis from "../moralis";
import upwork from "../images/upwork.png";
import linkedin from "../images/linkedin.png";
import telegram from "../images/telegram.svg";
import metamask from "../images/mask.png";
import walletConnect from "../images/wallet.png";

function Nav() {
  const dispatch = useDispatch();

  const { connectUser } = bindActionCreators(actionCreators, dispatch);

  const connectMetamask = async () => {
    console.log("hola");
    const user = await Moralis.Web3.authenticate();

    const userAddress = user.get("ethAddress");

    connectUser({ user: userAddress, wallet: "metamask" });
  };

  const connectWalletConnect = async () => {
    const user = await Moralis.Web3.authenticate({
      provider: "walletconnect",
      chainId: 4,
    });

    const userAddress = user.get("ethAddress");

    connectUser({ user: userAddress, wallet: "walletConnect" });
  };
  return (
    <AppBar position="relative" style={{ "margin-bottom": "12px" }}>
      <Grid
        container
        direction="row"
        justifyContent="space-between"
        alignItems="flex-start"
      >
        <Grid className="nav-icons left">
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={() => connectMetamask()}
          >
            <img className="logo" src={metamask} alt="metamask logo" />
          </IconButton>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={() => connectWalletConnect()}
          >
            <img
              className="logo wallet"
              src={walletConnect}
              alt="walletConnect logo"
            />
          </IconButton>
          <Button>
            <Link className="nav-link" to="/">
              IPFS Uploader
            </Link>
          </Button>
          <Button>
            <Link className="nav-link" to="/mint">
              NFT Minter
            </Link>
          </Button>
          <Button>
            <Link className="nav-link" to="/solana">
              Solana Candy Machine
            </Link>
          </Button>
        </Grid>
        <Grid className="nav-icons right">
          <a
            href="https://www.upwork.com/freelancers/~016bf7bd9e5cf2ae82"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img className="logo" src={upwork} alt="upwork logo" />
          </a>
          <br />
          <a
            href="https://www.linkedin.com/in/juanochoa8a/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img className="logo" src={linkedin} alt="linkedin logo" />
          </a>

          <a
            href="https://t.me/manu_8a"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img className="logo" src={telegram} alt="telegram logo" />
          </a>
        </Grid>
      </Grid>
    </AppBar>
  );
}

export default Nav;
