import contract from "./interface";
import Web3 from "./web3";

export const mintNFT = async (_uri) => {
  try {
    let web3 = await Web3();
    let instance = await contract();
    const accounts = await web3.eth.getAccounts();
    const result = await instance.methods
      .mint(accounts[0], _uri)
      .send({ from: accounts[0] });

    return result;
  } catch (error) {
    console.log(error);
  }
};

export const batchMintNFT = async (_uri) => {
  try {
    let web3 = await Web3();
    let instance = await contract();
    const accounts = await web3.eth.getAccounts();
    const result = await instance.methods
      .mintBatch(accounts[0], _uri)
      .send({ from: accounts[0] });

    return result;
  } catch (error) {
    console.log(error);
  }
};
