const initialState = {
  JSONfinish: [],
  userAddress: "",
  walletType: "",
  JSONuploaded: [],
  userAddress: "",
  connectionType: "",
  consoleMsg: [
    {
      msg: "Welcome to NFT Uploader",
      type: "success",
    },
  ],
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case "deposit":
      return state + action.payload;
    case "withdraw":
      return state - action.payload;
    case "LOAD_JSON":
      return {
        ...state,
        loading: false,
        JSONuploaded: action.payload,
      };
    case "ADD_TO_FINISH":
      return {
        ...state,
        JSONfinish: action.payload,
      };
    case "CLEAR_STATE":
      return {
        ...state,
        JSONfinish: [],
        JSONuploaded: [],
        consoleMsg: [],
      };
    case "ADD_CONSOLE_MSG":
      return {
        ...state,
        consoleMsg: action.payload,
      };
    case "USER_CONNECTED":
      return {
        ...state,
        userAddress: action.payload.user,
        connectionType: action.payload.wallet,
      };
    default:
      return state;
  }
};

export default reducer;
