import { store } from "../store";

export const depositMoney = (amount) => {
  return (dispatch) => {
    dispatch({
      type: "deposit",
      payload: amount,
    });
  };
};

export const withdrawMoney = (amount) => {
  return (dispatch) => {
    dispatch({
      type: "withdraw",
      payload: amount,
    });
  };
};

export const uploadJSON = (json) => {
  return (dispatch) => {
    dispatch({
      type: "LOAD_JSON",
      payload: json,
    });
  };
};

export const updateFinalJSON = (json) => {
  return (dispatch) => {
    dispatch({
      type: "ADD_TO_FINISH",
      payload: json,
    });
  };
};

export const updateConsole = (message) => {
  return (dispatch) => {
    dispatch({
      type: "ADD_CONSOLE_MSG",
      payload: message,
    });
  };
};

export const clearState = () => {
  return (dispatch) => {
    dispatch({
      type: "CLEAR_STATE",
    });
  };
};

export const connectUser = (data) => {
  return (dispatch) => {
    dispatch({
      type: "USER_CONNECTED",
      payload: data,
    });
  };
};

export const addToFinish = (object) => {
  return async (dispatch) => {
    let uploaded = await store.getState().account.JSONfinish;
    uploaded.push(object);

    dispatch(updateFinalJSON(uploaded));
  };
};

export const addMessage = (msg, type) => {
  return async (dispatch) => {
    let tempConsole = await store.getState().account.consoleMsg;
    tempConsole.push({ msg, type });

    dispatch(updateConsole(tempConsole));
  };
};
