import React, { useState, useEffect } from "react";
import Moralis from "../moralis";
import { create } from "ipfs-http-client";
import { client } from "../ipfs";
import { useSelector, useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../state/index";
import {
  Grid,
  Paper,
  Box,
  TextField,
  Button,
  Select,
  MenuItem,
  LinearProgress,
  Divider,
} from "@material-ui/core";

// const ipfsClient = create("https://ipfs.infura.io:5001/api/v0");

function Home() {
  const account = useSelector((state) => state);
  const dispatch = useDispatch();
  const JSONfile = useSelector((state) => state.account.JSONuploaded);
  const finalJSON = useSelector((state) => state.account.JSONfinish);
  const msgConsole = useSelector((state) => state.account.consoleMsg);
  const user = useSelector((state) => state.account.userAddress);
  const [selectedKey, setSelectedKey] = useState("");
  const [filesToUpload, setFilesToUpload] = useState([]);
  const [collectionName, setcollectionName] = useState("");
  const [nameLock, setNameLock] = useState(false);
  const [progress, setProgress] = useState(0);
  const ipfsBaseMetaUrl = "ipfs://";
  const ipfsBaseImgUrl = "https://ipfs.moralis.io:2053/ipfs/";
  const [isLoading, setIsLoading] = useState(false);
  let count = 0;

  const { uploadJSON, addToFinish, clearState, addMessage } =
    bindActionCreators(actionCreators, dispatch);

  useEffect(() => {
    console.log(account);
  }, []);

  let keys;
  if (JSONfile.length > 0) {
    keys = Object.keys(JSONfile[0]);
  }

  const handleJSON = (e) => {
    const fileReader = new FileReader();
    fileReader.readAsText(e.target.files[0], "UTF-8");
    fileReader.onload = (e) => {
      let file = JSON.parse(e.target.result);
      console.log(file);
      if (file.length > 0) {
        uploadJSON(file);
        addMessage("JSON file loaded", "info");
      } else {
        addMessage("Error: JSON file needs to contain an array", "error");
      }
    };
  };

  const downloadJSON = () => {
    const blob = new Blob([JSON.stringify(finalJSON)], { type: "text/json" });
    const a = document.createElement("a");
    a.download = `${collectionName}.json`;
    a.href = window.URL.createObjectURL(blob);
    const clickEvt = new MouseEvent("click", {
      view: window,
      bubbles: true,
      cancelable: true,
    });
    a.dispatchEvent(clickEvt);
    a.remove();
  };

  const checkCollectionName = async () => {
    const Collection = Moralis.Object.extend(`${collectionName}`);
    const query = new Moralis.Query(Collection);

    const results = await query.find();
    if (results.length > 0) {
      addMessage("Collection name already taken.", "error");
    } else {
      setNameLock(true);
      addMessage("Collection name is available!!", "success");
    }
    console.log(results);
  };

  const addImageToIpfs = async (object) => {
    try {
      const file = new Moralis.File(object.name, object);
      await file.saveIPFS();
      console.log(file);
      return file;
    } catch (error) {
      console.log(error);
      addMessage(`${error.msg}`, "error");
    }
  };

  const addObjectToIpfs = async (object) => {
    try {
      const file = new Moralis.File("file.json", {
        base64: btoa(JSON.stringify(object)),
      });
      await file.saveIPFS();
      console.log(file);
      return file;
    } catch (error) {
      console.log(error);
      addMessage(`${error.msg}`, "error");
    }
  };

  const handleUpload = async () => {
    setIsLoading(true);
    count = 0;
    addMessage("start ipfs uploading.", "info");

    if (filesToUpload.length > 10) {
      addMessage(
        `This is a demonstration app, the limit is set to 10 images. please contact the site developper for assistance`,
        "error"
      );
    } else {
      try {
        filesToUpload.map(async (item, index) => {
          addMessage(`uploading image N°${index + 1}`, "info");
          let imgUploaded = await addImageToIpfs(item.file);
          item.info.image = `${ipfsBaseImgUrl}${imgUploaded.hash()}`;
          addMessage(`image N°${index + 1} uploaded.`, "info");

          addMessage(`uploading metadata N°${index + 1}`, "info");
          const metadata = item.info;
          let metaUploaded = await addObjectToIpfs(metadata);
          item.info.metadata = `${ipfsBaseMetaUrl}${metaUploaded.hash()}`;
          saveToDatabase({ ...item.info, tokenId: index, minted: false });
          addMessage(`metadata N°${index + 1} uploaded.`, "info");

          addToFinish({
            id: index,
            image: item.info.image,
            uri: `${ipfsBaseMetaUrl}${metaUploaded.hash()}`,
            metaHash: metaUploaded.hash(),
          });

          if (metaUploaded) {
            count++;
            let newProgress = (count * 100) / filesToUpload.length;
            setProgress(newProgress);
            if (count === filesToUpload.length) {
              addMessage(`Finish! ${count} images uploaded.`, "success");
            }
          }
        });

        const Collection = Moralis.Object.extend("Collections");
        const collection = new Collection();
        collection.save({
          name: `${collectionName}`,
          items: filesToUpload.length,
          creator: user,
        });
      } catch (error) {
        console.log(error);
        addMessage(`${error.msg}`, "error");
      }
    }

    setIsLoading(false);
  };

  const saveToDatabase = async (data) => {
    addMessage("saving to database.", "info");
    try {
      const Collection = Moralis.Object.extend(`${collectionName}`);
      const collection = new Collection();
      collection.save(data);

      addMessage("saved to database.", "info");
    } catch (error) {
      console.log(error);
      addMessage(`${error.msg}`, "error");
    }
  };

  const handleImagesUpload = (e) => {
    let files = e.target.files;
    console.log(files);
    let result = [];
    let names = [];
    for (let i = 0; i < files.length; i++) {
      let info = JSONfile.find(
        (meta) => meta[selectedKey] == files[i].name.replace(/\.[^/.]+$/, "")
      );
      if (!info) {
        addMessage(
          `Not found selected key on metadata for file: ${files[i].name}.`,
          "error"
        );
      } else {
        let file = files[i];
        names.push(file.name);
        result.push({ info, file });
      }
    }
    if (result.lenght !== 0) {
      addMessage(`Found ${result.length} images with metadata info.`, "info");
      addMessage(`Ready to upload ${result.length} images.`, "success");
    }

    setFilesToUpload(result);
  };

  const startAgain = () => {
    clearState();
    setFilesToUpload([]);
    setSelectedKey("");
    setcollectionName("");
    setNameLock(false);
    setProgress(0);
  };

  return (
    <Grid container spacing={3} className="main-container">
      <Grid
        xs={12}
        md={6}
        className="left-container"
        container
        direction="column"
        justifyContent="space-between"
        alignItems="center"
      >
        <h1>NFT Uploader</h1>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="flex-end"
        >
          <Box>
            <h3>Choose a Name for your Collection </h3>
            <h5>Only letters and numbers without spaces.</h5>
            <TextField
              onkeypress="return event.charCode != 32"
              label="Collection Name"
              variant="outlined"
              value={collectionName}
              onChange={(e) =>
                setcollectionName(
                  e.target.value
                    .replace(/[!&\/\\#,+()$~%.'":*?<>{}-]/g, "")
                    .split(" ")
                    .join("")
                )
              }
              disabled={nameLock}
            />
          </Box>
          <Box>
            {nameLock ? (
              <Button
                variant="outlined"
                style={{ "margin-bottom": "10px" }}
                onClick={() => setNameLock(false)}
              >
                Change Name
              </Button>
            ) : (
              <Button
                variant="outlined"
                style={{ "margin-bottom": "10px" }}
                onClick={() => checkCollectionName()}
              >
                Check availability
              </Button>
            )}
          </Box>
        </Grid>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="baseline"
        >
          <Box>
            <h3>Upload JSON File </h3>
            <Button
              variant="outlined"
              className="button-select"
              disabled={!nameLock}
            >
              <label>
                Select File
                <input
                  type="file"
                  accept="application/JSON"
                  style={{ display: "none" }}
                  onChange={handleJSON}
                />
              </label>
            </Button>
            <br />
            {JSONfile && `there is ${JSONfile.length} items in the JSON file`}
          </Box>

          <Box>
            <h4>
              Choose a key
              <br /> value should match image name
            </h4>
            <Select
              className="form-control"
              onChange={(e) => setSelectedKey(e.target.value)}
              disabled={JSONfile.length === 0 ? true : false}
            >
              {keys &&
                keys.length !== 0 &&
                keys.map((item, index) => {
                  return (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  );
                })}
            </Select>
          </Box>
        </Grid>

        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
        >
          <Box>
            <h3>Upload Images </h3>
            <Button
              variant="outlined"
              className="button-select"
              disabled={selectedKey ? false : true}
            >
              <label>
                Select Images
                <input
                  type="file"
                  multiple
                  style={{ display: "none" }}
                  accept="image/png, image/jpeg"
                  onChange={(e) => handleImagesUpload(e)}
                />
              </label>
            </Button>
            <br />
          </Box>
          <Box>
            {filesToUpload &&
              `there is ${filesToUpload.length} matches in the JSON file`}
          </Box>
        </Grid>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
        >
          <Box>
            <Button
              variant="outlined"
              className="button-select"
              disabled={user && !isLoading && filesToUpload.length > 0 ? 0 : 1}
              onClick={() => handleUpload()}
            >
              {isLoading ? "Uploading..." : "Upload to IPFS"}
            </Button>
            {!user && <h5>Please connect your wallet to upload.</h5>}
          </Box>
        </Grid>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
        >
          <Button
            variant="outlined"
            className="button-select"
            disabled={finalJSON.length > 0 ? 0 : 1}
            onClick={() => downloadJSON()}
          >
            download JSON file
          </Button>

          <Button
            variant="outlined"
            className="button-select"
            onClick={() => startAgain()}
          >
            Start Again
          </Button>
        </Grid>

        <LinearProgress
          variant="determinate"
          value={progress}
          color={progress === 100 ? "primary" : "secondary"}
          className="progress"
        />
        <Divider orientation="vertical" />
      </Grid>
      <Grid xs={0} md={6} className="right-container">
        <Grid
          className="console-container"
          container
          direction="column"
          // justifyContent="center"
          alignItems="center"
        >
          <h3>Console</h3>
          <Paper elevation={3} className="console">
            <Grid container direction="column-reverse">
              {msgConsole.map((msg) => {
                return <h5 className={msg.type}>{msg.msg}</h5>;
              })}
            </Grid>
          </Paper>
        </Grid>
        <Divider />
        <Grid
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          className="img-container"
        >
          {finalJSON.length > 0 &&
            finalJSON.map((item, index) => {
              return (
                <Box component="span" className="image-result">
                  <a
                    href={`${ipfsBaseImgUrl}${item.metaHash}`}
                    key={index}
                    target="_blank"
                  >
                    <img
                      src={item.image}
                      key={index}
                      alt={index}
                      style={{ maxHeight: "200px", maxWidth: "200px" }}
                    />
                  </a>
                </Box>
              );
            })}
        </Grid>
        <h3 className="total-text">
          Total images uploaded : {finalJSON.length}
        </h3>
      </Grid>
    </Grid>
  );
}

export default Home;
